.transfer-modal .modal-header {
  width: 100%;
  height: 115;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  padding-bottom: 20px;
}
.transfer-modal .modal-header .ant-select-selection {
  border: none !important;
}
.transfer-modal .modal-header .ant-select-open .ant-select-selection,
.transfer-modal .modal-header .ant-select-selection:focus {
  box-shadow: none;
}
.transfer-modal .transfer-center {
  position: relative;
}
.transfer-modal .ant-select-selection--multiple {
  min-height: 48px;
}
