.my-assets .personal-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 32px;
  color: #000;
}
.my-header .my-all {
  width: 853px;
  height: 175px;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding-top: 24px;
  margin-bottom: 24px;
}
.my-header .my-all .all-left {
  margin-left: 40px;
}
.my-header .my-all .all-left .all-left-num {
  font-size: 20px;
  color: #0091FF;
  font-weight: 800;
  margin-top: 10px;
}
.my-header .my-all .all-left .all-left-nums {
  margin-top: 15px;
  font-size: 12px;
  margin-right: 10px;
}
.my-header .my-all .all-left .all-left-nums .color-hed {
  padding: 0 2px;
  color: #0091FF;
  font-weight: 600;
}
.my-header .my-all .all-right {
  position: absolute;
  right: 20px;
  top: 0;
  width: 102px;
  height: 34px;
  border-radius: 4px;
  border: 1px solid #0091ff;
  text-align: center;
  line-height: 34px;
  color: #0091ff;
  margin-top: 8px;
  margin-left: 50px;
  cursor: pointer;
}
.my-header .my-all .flow-btn {
  padding: 0 10px;
  height: 27px;
  background: #e3f3ff;
  border-radius: 2px;
  text-align: center;
  line-height: 27px;
  font-weight: 500;
  color: #0091ff;
  margin-right: 16px;
  margin-top: 10px;
  cursor: pointer;
}
.my-header .flow-account {
  height: 183px;
}
.my-header .flow-account .all-left .all-left-num {
  font-size: 20px;
  color: #0091FF;
  font-weight: 800;
  margin-top: 10px;
}
.my-header .flow-account .all-right {
  position: absolute;
  right: 20px;
  top: 0;
  width: 102px;
  height: 34px;
  border-radius: 4px;
  border: 1px solid #0091ff;
  text-align: center;
  line-height: 34px;
  color: #0091ff;
  margin-top: 8px;
  margin-left: 50px;
  cursor: pointer;
}
.my-header .flow-accounts {
  width: 853px;
  height: 140px;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding-top: 24px;
  margin-bottom: 24px;
}
.my-header .flow-accounts .all-left {
  margin-left: 40px;
}
.my-header .flow-accounts .all-left .all-left-num {
  font-size: 12px;
  margin-top: 10px;
}
.my-header .flow-accounts .all-left .all-left-nums {
  margin-top: 10px;
