}
.my-header .flow-accounts .all-left .all-left-nums .color-hed {
  padding: 0 2px;
  color: #0091FF;
  font-weight: 600;
}
.my-header .flow-accounts .all-left .flow-btn {
  padding: 0 10px;
  height: 27px;
  background: #e3f3ff;
  border-radius: 2px;
  text-align: center;
