@media only screen and (max-width: 768px) {
  .my-header .my-all {
    width: 100%;
    padding-top: 12px;
  }
  .my-header .my-all .all-left {
    width: 100%;
    margin-top: 15px;
  }
  .my-header .my-all .all-left-num {
    padding-top: 5px;
  }
  .my-header .flow-account {
    width: 100%;
  }
  .my-header .flow-accounts {
    width: 100%;
  }
  .my-header .flow-accounts .all-left {
    margin-top: 5px;
  }
  .my-header .flow-accounts .all-right {
    top: -10px;
  }
}
